import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

import Home from '@/pages/Home'

export default new Router({
  mode: 'history',
  base: process.env.NODE_ENV === 'production' ? '/oac-review' : '/',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    }
  ]
})



